import Head from "../../components/head";
import HomeClients from "./clients";
import HomeLibraries from "./libraries";
import HomePlatforms from "./platforms";
import Humanize from "../../components/humanize";
import { Link } from "react-router-dom";
import Navbar from "../../navbar";
import { ReactComponent as Shape2 } from "../../media/shapes/shape-2.svg";
import Spinner from "../../components/spinner";
import { useSearchResult } from "../../data";

const description =
  "The world's first package and project dependency management solution for embedded development";

export default function HomeMain() {
  const trendingLibraries = useSearchResult({
    sort: "trending",
    type: "library",
  });
  return (
    <main className="main-home">
      <Head title="PlatformIO Registry" description={description} />
      <section className="section-hero">
        <Navbar />
        <div className="container py-4 pt-md-5">
          <div className="w-md-75 w-lg-65 mx-md-auto text-white text-center py-lg-4">
            <h1 className="display-4 fw-bold">PlatformIO Registry</h1>
            <h5 className="opacity-75 fw-normal pt-3">{description}</h5>
            <div className="hstack gap-2 justify-content-center py-4">
              <div>
                <Link to="/search" className="btn btn-success btn-lg">
                  Browse Packages
                </Link>
              </div>
            </div>
            <p>
              <small className="opacity-50">
                {renderRegistryStats(trendingLibraries)}
              </small>
            </p>
          </div>
        </div>
        <Shape2 className="d-none d-md-block" />
      </section>

      <HomeClients />
      <HomePlatforms items={(trendingLibraries.result || {}).items} />
      <HomeLibraries items={(trendingLibraries.result || {}).items} />
    </main>
  );
}

function renderRegistryStats(trendingLibraries) {
  if (!trendingLibraries.result) {
    return <Spinner />;
  }
  return (
    <>
      <Humanize type="counter" value={trendingLibraries.result.total} />{" "}
      libraries, 49 development platforms &amp; counting
    </>
  );
}
