import { TIER_OFFICIAL_TITLE, TIER_VERIFIED_TITLE } from "../../constants";
import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Truncate from "../../components/truncate";
import { enableBStooltips } from "../../helpers";
import { makePackageUrl } from "../package/main";
import { makeSearchUrl } from "./main";
import { useEffect } from "react";

export default function SearchResults(props) {
  useEffect(() => {
    enableBStooltips();
  });
  return (
    <ul className="list-group list-group-flush">
      {props.items.map((item) => (
        <li key={item.id} className="list-group-item d-flex py-4">
          <div
            className="flex-shrink-0 me-2 pt-1"
            title={item.type.toUpperCase()}
          >
            <Icon name={`package-${item.type}`} />
          </div>
          <div className="flex-grow-1 text-break">
            <div className="hstack flex-wrap gap-3 align-self-center">
              <div>
                <h3 className="h4">
                  <Link
                    to={makePackageUrl({
                      type: item.type,
                      ownername: item.owner.username,
                      name: item.name,
                    })}
                  >
                    {item.owner.username}/{item.name}
                  </Link>
                </h3>
              </div>
              {item.tier === "official" ? (
                <div>
                  <sup className="h6">
                    <span
                      className="badge bg-official"
                      data-bs-toggle="tooltip"
                      title={TIER_OFFICIAL_TITLE}
                    >
                      <div className="d-flex flex-row align-items-center">
                        <div>
                          <Icon name="package-official" size="14" />
                        </div>
                        <div className="ps-1">OFFICIAL</div>
                      </div>
                    </span>
                  </sup>
                </div>
              ) : null}
              {item.tier === "verified" ? (
                <div>
                  <sup className="h6">
                    <span
                      className="badge bg-success"
                      data-bs-toggle="tooltip"
                      title={TIER_VERIFIED_TITLE}
                    >
                      <div className="d-flex flex-row align-items-center">
                        <div>
                          <Icon name="package-verified" size="14" />
                        </div>
                        <div className="ps-1">VERIFIED</div>
                      </div>
                    </span>
                  </sup>
                </div>
              ) : null}
            </div>
            <p className="mb-1 text-break">
              <Truncate
                value={item.description}
                max={255}
                moreTitle="Read more →"
              />
            </p>
            <div className="mb-2">
              {item.keywords.map((keyword) => (
                <span
                  key={keyword}
                  className="badge rounded-pill text-primary bg-info me-2"
                >
                  <Link to={makeSearchUrl({ query: { keyword } })}>
                    {keyword}
                  </Link>
                </span>
              ))}
            </div>
            <small>
              <ul className="list-inline text-muted">
                <li
                  className={`list-inline-item me-1 ${
                    item.popularity_trend < 0 ? "text-danger" : "text-success"
                  }`}
                  title="Popularity"
                >
                  <Icon
                    name={item.popularity_trend < 0 ? "chart-down" : "chart-up"}
                    size="18"
                  />
                </li>
                <li className="list-inline-item" title="Popularity">
                  <Humanize type="counter" value={item.popularity_rank} />
                </li>
                {item.popularity_rank <=
                (item.type === "platform" ? 10 : 100) ? (
                  <>
                    <li className="list-inline-item">·</li>
                    <li className="list-inline-item">
                      <Icon name="heart" className="me-1" />
                      TOP {item.type === "platform" ? 10 : 100}
                    </li>
                  </>
                ) : null}
                {item.stars_count ? (
                  <>
                    <li className="list-inline-item">·</li>
                    <li className="list-inline-item" title="Stars">
                      <Icon name="star" className="me-1" />
                      <Humanize type="counter" value={item.stars_count} />
                    </li>
                  </>
                ) : null}
                <li className="list-inline-item">·</li>
                <li className="list-inline-item">
                  Published{" "}
                  <Humanize type="time" value={item.version.released_at} />
                </li>
              </ul>
            </small>
          </div>
        </li>
      ))}
    </ul>
  );
}

SearchResults.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      type: PropTypes.string.isRequired,
      tier: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      owner: PropTypes.shape({
        username: PropTypes.string.isRequired,
      }).isRequired,
      keywords: PropTypes.arrayOf(PropTypes.string),
      stars_count: PropTypes.number.isRequired,
      popularity_rank: PropTypes.number.isRequired,
      popularity_trend: PropTypes.number.isRequired,
      version: PropTypes.shape({
        released_at: PropTypes.string.isRequired,
      }).isRequired,
    })
  ),
};
