import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Spinner from "../../components/spinner";
import Truncate from "../../components/truncate";
import { makePackageUrl } from "../package/main";

export default function HomeLibraries(props) {
  return (
    <section className="section-libraries border-top">
      <div className="container py-5">
        <div className="w-md-75 w-lg-65 mx-auto text-center pt-3">
          <div className="badge bg-secondary fs-6 fw-bold">
            <Icon name="package-library" className="me-1" size="18" /> LIBRARIES
          </div>
          <h2 className="h1 pt-4 pb-2">
            The world's <span className="text-success">largest registry</span>{" "}
            for embedded libraries
          </h2>
          <p className="text-muted pb-3">
            Browse over 10,000 libraries from software vendors, open-source
            projects, and the community.
          </p>
          <ul className="list-inline">
            <li className="list-inline-item pe-2 pb-2">
              <Link
                to="/search?t=library"
                className="btn btn-success"
                style={{ minWidth: "200px" }}
              >
                Browse All
              </Link>
            </li>
            <li className="list-inline-item ps-2">
              <a href="https://docs.platformio.org/en/latest/librarymanager/index.html">
                Library Management <Icon name="chevron-right" />
              </a>
            </li>
          </ul>
        </div>
        <div className="hstack pt-5 pb-2">
          <div className="flex-fill">
            <h3>Featured Libraries</h3>
          </div>
          <div>
            <Link to="/search?t=library">See All</Link>
          </div>
        </div>
        {renderTrendingItems(props.items)}
        <div className="pt-5 text-md-center">
          <ul className="list-md-inline">
            <li className="list-inline-item">
              <a href="https://docs.platformio.org/en/latest/librarymanager/config.html">
                <Icon name="braces" className="text-dark me-2" />
                library.json
              </a>
            </li>
            <li className="list-inline-item px-4 text-muted">·</li>
            <li className="list-inline-item">
              <a href="https://docs.platformio.org/en/latest/librarymanager/creating.html">
                <Icon name="pencil" className="text-dark me-2" />
                Create a library
              </a>
            </li>
            <li className="list-inline-item px-4 text-muted">·</li>
            <li className="list-inline-item">
              <a href="https://docs.platformio.org/en/latest/core/userguide/package/cmd_publish.html#cmd-package-publish">
                <Icon name="upload" className="text-dark me-2" />
                Publish a library
              </a>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
}

function renderTrendingItems(items) {
  if (!items) {
    return <Spinner />;
  }
  return (
    <div className="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-4">
      {items.slice(0, 6).map((item) => (
        <div className="col" key={item.id}>
          <div className="card h-100">
            <div className="card-body">
              <h5 className="card-title">
                <Link
                  to={makePackageUrl({
                    type: item.type,
                    ownername: item.owner.username,
                    name: item.name,
                  })}
                >
                  {item.name}
                </Link>
              </h5>
              <p className="card-text text-break">
                <Truncate value={item.description} max={100} moreTitle="" />
              </p>
            </div>
            <div className="card-footer">
              <small>
                <ul className="list-inline text-muted m-0">
                  <li
                    className={`list-inline-item me-1 ${
                      item.popularity_trend < 0 ? "text-danger" : "text-success"
                    }`}
                    title="Popularity"
                  >
                    <Icon
                      name={
                        item.popularity_trend < 0 ? "chart-down" : "chart-up"
                      }
                      size="18"
                    />
                  </li>
                  <li className="list-inline-item" title="Popularity">
                    <Humanize type="counter" value={item.popularity_rank} />
                  </li>
                  {item.popularity_rank <= 100 ? (
                    <>
                      <li className="list-inline-item">·</li>
                      <li className="list-inline-item">
                        <Icon name="heart" className="me-1" />
                        TOP 100
                      </li>
                    </>
                  ) : null}
                  {item.stars_count ? (
                    <>
                      <li className="list-inline-item">·</li>
                      <li className="list-inline-item" title="Stars">
                        <Icon name="star" className="me-1" />
                        <Humanize type="counter" value={item.stars_count} />
                      </li>
                    </>
                  ) : null}
                  <li className="list-inline-item">·</li>
                  <li className="list-inline-item">
                    <Humanize type="time" value={item.version.released_at} />
                  </li>
                </ul>
              </small>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

HomeLibraries.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number.isRequired,
      type: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      owner: PropTypes.shape({
        username: PropTypes.string.isRequired,
      }).isRequired,
      keywords: PropTypes.arrayOf(PropTypes.string),
      stars_count: PropTypes.number.isRequired,
      popularity_rank: PropTypes.number.isRequired,
      popularity_trend: PropTypes.number.isRequired,
      version: PropTypes.shape({
        released_at: PropTypes.string.isRequired,
      }).isRequired,
    })
  ),
};
